<template>
  <div class="info__other">
    <div class="info__other-inp">
      <input
        type="text"
        :name="item.nameInput"
        v-model="valueInput"
        @change="changeOtherInfo"
        @focus="isChoosedImput = true"
        @blur="
              valueInput.length > 0
                ? (isChoosedImput = true)
                : (isChoosedImput = false)
            "
      />
      <label :class="{ active: isChoosedImput }">{{item.nameInput}}</label>
      <span>{{ errors.errors }}</span>
    </div>
  </div>
</template>

<script>
import {mapMutations, mapGetters} from "vuex"
export default {
  name: "InputOther",
  props: {
    item: {
      type: Object,
      default: () => ({}),
    },
  },
  data() {
    return {
      errors: {},
      isChoosedImput: false,
      valueInput: ''
    }
  },
  computed: {
    ...mapGetters(['GET_CONTACTS']),
  },
  created() {
    this.fetchData();
  },
  watch: {
    $route: "fetchData",
  },
  mounted() {
    this.inputEmpty();
  },
  methods: {
    ...mapMutations(['ADD_OTHER_INFO']),
    changeOtherInfo() {
      if (this.valueInput.length >= 3) {
        this.errors = {};
        this.ADD_OTHER_INFO({
          id: this.item.id,
          value: this.valueInput,
        });
      } else if (this.valueInput.length < 3) {
        this.errors = { ...this.errors, error: "Некоректные данные" };
      }
    },
    inputEmpty() {
      if (this.valueInput.length > 0) {
        this.isChoosedImput = true;
      }
    },
    fetchData() {
      this.valueInput = this.item.value
    },
  }
};
</script>

<style lang="less">
.info__other {
  label {
    position: absolute;
    left: 3px;
    top: 0;
    font-size: @size-main;
    transition: ease all 0.3s;

    &.active {
      top: initial;
      bottom: 3px;
      font-size: @size-light;
    }
  }

  &--inp {
    margin-bottom: 15px;
    position: relative;

    input::-webkit-outer-spin-button,
    input::-webkit-inner-spin-button {
      -webkit-appearance: none;
    }
  }
}
</style>
