<template>
  <div class="list-fields">
    <ul>
      <li
        v-for="(item, key) in GET_LIST"
        :key="key"
        :data-id="item.id"
        @click="chooseListFields"
      >
        {{ item.name }}
      </li>
    </ul>
  </div>
</template>

<script>
import { mapMutations, mapGetters } from "vuex";
export default {
  name: "ListFields",
  computed: { ...mapGetters(["GET_LIST"]) },
  methods: {
    ...mapMutations(["SET_OTHER_INFO", "DELETE_LIST"]),
    chooseListFields(e) {
      this.SET_OTHER_INFO({
        id: e.target.dataset.id,
        nameInput: e.target.innerText,
      });
      this.DELETE_LIST({
        id: e.target.dataset.id
      });
    },
  },
};
</script>

<style lang="less">
.list-fields {
  display: none;
  overflow: hidden;
  ul {
    list-style: none;
    padding: 0 0 15px 0;
    margin: 0;
    li {
      cursor: pointer;
      font-size: @size-large;
      padding-bottom: 15px;
    }
  }

  &__active {
    display: block;
  }

  &__active ul {
    animation: slideDown 0.3s linear;
  }
}

@keyframes slideDown {
  0% {
    opacity: 0;
    transform: translateY(-100%);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
</style>
