<template>
  <div class="info container">
    <div class="info__photo">
      <div class="info__photo-bg">
        <img src="@/assets/images/s1200.webp" alt="" />
      </div>
      <h1>{{ lastName }} {{ firstName }}</h1>
      <div class="info__avatar">
        <img src="@/assets/images/s1200.webp" alt="" />
      </div>
    </div>
    <div class="info__wrapper">
      <div class="info__required-fields">
        <div class="info__required-fields--inp">
          <input
            type="text"
            name="firstName"
            v-model="firstName"
            @change="changeMainInfo"
            @focus="isChoosedFirstName = true"
            @blur="
              firstName.length > 0
                ? (isChoosedFirstName = true)
                : (isChoosedFirstName = false)
            "
          />
          <label :class="{ active: isChoosedFirstName }">first name</label>
          <span>{{ errors.errorFirstName }}</span>
        </div>
        <div class="info__required-fields--inp">
          <input
            type="text"
            name="lastName"
            v-model="lastName"
            @change="changeMainInfo"
            @focus="isChoosedLastName = true"
            @blur="
              lastName.length > 0
                ? (isChoosedLastName = true)
                : (isChoosedLastName = false)
            "
          />
          <label :class="{ active: isChoosedLastName }">last name</label>
        </div>
        <div class="info__required-fields--inp">
          <input
            type="number"
            name="phone"
            v-model="phone"
            @change="changeMainInfo"
            @focus="isChoosedPhone = true"
            @blur="
              phone.length > 0
                ? (isChoosedPhone = true)
                : (isChoosedPhone = false)
            "
          />
          <label :class="{ active: isChoosedPhone }">phone</label>
          <span>{{ errors.errorPhone }}</span>
        </div>
      </div>
      <div class="info__item">
        <input-other
          v-for="(item, key) in GET_OTHER_INFO"
          :key="key"
          :item="item"
        />
        <list-fields :class="{ 'list-fields__active': isListFields }" />
        <div
          @click="slideList"
          v-if="GET_LIST.length > 0"
          class="info__other-button"
        >
          {{ isListFields ? "отмена" : "добавить поле" }}
        </div>
      </div>
      <div class="info__add">
        <router-link
          to="/"
          @click.native="saveContact"
          :class="{ disabled: isButtonSaveDisabled }"
          >Сохранить</router-link
        >
        <router-link to="/" @click.native="cancelPage">Отмена</router-link>
        <router-link to="/" @click.native="deleteContact">delete</router-link>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";
import ListFields from "@/components/ListFields";
import InputOther from "@/components/InputOther";

export default {
  name: "Information",
  components: {
    ListFields,
    InputOther,
  },
  data() {
    return {
      errors: {},
      firstName: "",
      lastName: "",
      phone: "",
      isChoosedFirstName: false,
      isChoosedLastName: false,
      isChoosedPhone: false,
      isListFields: false,
      file: "",
    };
  },
  computed: {
    ...mapGetters([
      "GET_OTHER_INFO",
      "GET_MAIN_INFO",
      "GET_ID_CONTACT",
      "GET_CONTACTS",
      "GET_LIST",
    ]),
    isButtonSaveDisabled() {
      return !this.firstName || !this.phone;
    },
  },
  created() {
    this.GET_STORE_CONTACTS();
    this.fetchData();
  },
  watch: {
    $route: "fetchData",
  },
  mounted() {
    this.inputEmpty();
    this.GET_OTHER_INFO.forEach((element) => {
      this.GET_LIST.forEach((el) => {
        if (element.nameInput === el.name) {
          this.DELETE_LIST({
            id: element.id,
          });
        }
      });
    });
  },
  methods: {
    ...mapMutations([
      "SET_MAIN_INFO",
      "SET_FULL_CONTACT",
      "SET_OTHER_INFO",
      "CLEAR_MAIN_INFO",
      "CLEAR_OTHER_INFO",
      "SET_INFORMATION_FROM_CONTACTS",
      "GET_STORE_CONTACTS",
      "DELETE_CONTACT",
      "DELETE_LIST",
    ]),
    inputEmpty() {
      if (this.firstName.length > 0) {
        this.isChoosedFirstName = true;
      }
      if (this.lastName.length > 0) {
        this.isChoosedLastName = true;
      }
      if (this.phone.length > 0) {
        this.isChoosedPhone = true;
      }
    },
    slideList() {
      this.isListFields
        ? (this.isListFields = false)
        : (this.isListFields = true);
    },
    changeMainInfo() {
      if (this.firstName.length >= 3 && this.phone.length >= 5) {
        this.errors = {};
        this.SET_MAIN_INFO({
          firstName: this.firstName,
          lastName: this.lastName,
          phone: this.phone,
        });
      } else if (this.firstName.length < 3) {
        this.errors = { ...this.errors, errorFirstName: "Укажите имя" };
      } else if (this.phone.length < 5) {
        this.errors = {};
        this.errors = { ...this.errors, errorPhone: "Укажите телефон" };
      }
    },
    saveContact() {
      this.SET_FULL_CONTACT({
        id_contact: this.GET_ID_CONTACT,
        main: this.GET_MAIN_INFO,
        other: this.GET_OTHER_INFO,
      });
      this.CLEAR_OTHER_INFO([]);
      this.CLEAR_MAIN_INFO({});
    },
    cancelPage() {
      this.CLEAR_OTHER_INFO([]);
      this.CLEAR_MAIN_INFO({});
    },
    fetchData() {
      this.GET_CONTACTS.forEach((el) => {
        if (el.id_contact === this.$route.query.contact_id) {
          this.SET_INFORMATION_FROM_CONTACTS(el.other);
          this.firstName = el.main.firstName;
          this.lastName = el.main.lastName;
          this.phone = el.main.phone;
          this.SET_MAIN_INFO({
            firstName: el.main.firstName,
            lastName: el.main.lastName,
            phone: el.main.phone,
          });
        }
      });
    },
    deleteContact() {
      this.DELETE_CONTACT({ id_contact: this.GET_ID_CONTACT });
    },
  },
};
</script>

